.btn:not(.eff-reveal-border) {
    position: relative;
    display: inline-block;
    padding: $gap/2 $gap*1.5;
    background-color: $color-bg;
    border: 0;
    color: $color-text-1;
    transition: all 200ms ease;
    
    @include bg-gradient-gray();

    &:hover,
    &:focus {
        @include btn-hover();
    }
    &:active {
        @include btn-active();
    }


    &.btn-primary {
        @include bg-gradient();
        color: var(--color-light);

        &:hover,
        &:focus {
            @include animate-gradient();
            @include btn-hover(#30618d);
        }
        &:active {
            @include btn-active(#30618d);
        }
    }
}


.btn.eff-reveal-border {
    display: inline-block;
    background-color: $color-bg;
    background-image: none;
    overflow: hidden;
    transition: all 100ms ease;

    &> .eff-reveal {
        padding: $gap/2 $gap*1.5;
        background-color: $color-bg;
        border: 0;
        color: $color-text-1;
        transition: all 200ms ease;
    
    
        &:hover,
        &:focus {
            color: $color-text-1;
            text-decoration: none;
            outline: none;
        }
    }

    &:hover,
    &:focus {
        @include btn-hover();

        &> .eff-reveal {
            
        }
    }
    &:active {
        @include btn-active();
        
        &> .eff-reveal {
            
        }
    }



    &.btn-primary {
        background-image: none;

        &> .eff-reveal {
            @include bg-gradient();
            color: var(--color-light);
        }
    }

    
}
