.home-source {
    background-image: linear-gradient(-20deg, #1e1b28 0%, #397fad 100%);

    .home-source-content {
        background-image: url(../img/star-bg.svg);
        background-position: top center;
        background-size: cover;
        color: var(--color-light);

        padding-top: $gap*3;
        padding-bottom: $gap*3;

        .eff-reveal-border {
            border-radius: $gap*3;

            .eff-reveal {
                border-radius: $gap*3;
            }
        }
    }
}
